import React, { Component } from "react";
import Topbar from "../../components/Topbar/index";
import ReturnTop from "../../components/ReturnTop/index";
import Footer from "../../components/Footer/index";
import SideBar from "../../components/SideBar/index";
import { Link } from "react-router-dom";
import $ from "jquery";
import { connect } from "react-redux";
import { Button, Table, Modal, Dropdown } from "react-bootstrap";
import { country } from "../../utils/countryFreight";
import Pagination from "../../components/Pagination/index";
class B2c_platforms extends Component {
  constructor(props) {
    super(props);
    this.state = {
      islogin: false,
      show: false,
      platform: "",
      tipModalShow: false,
      tipMessage: "",
      shopee: {
        account: "",
        account2: "",
        recive: ""
      },
      amazon: {
        store_name: "",
        // nick_name: "",
        site: "BR",
        seller_id: "",
        token: "",
        status: 0
      },
      amazonPages: {
        page: 1,
        total: 1,
        pageSize: 20
      },
      amazonStatus: {
        store_name_status: "",
        seller_id_status: "",
        token_status: ""
      },
      amazoonData: [
        {
          usa_id: 1,
          user_id: 10,
          usa_online_status: 10,
          usa_auth_status: 10,
          usa_auth_code: "111",
          usa_quick_login: 1,
          usa_add_time: "2019-08-01 10:10:20",
          usa_update_time: "2019-08-01 10:10:23",
          usa_auth_time: "2019-08-01 10:10:25",
          usa_login_time: "2019-08-01 10:10:27",
          usa_logout_time: "2019-08-01 10:10:29"
        }
      ],
      siteList: [
        "BR",
        "CA",
        "MX",
        "US",
        //U.A.E.
        "AE",
        "DE",
        "ES",
        "FR",
        "GB",
        "IN",
        "IT",
        "TR",
        "AU",
        "JP"
      ]
    };
  }
  componentDidMount() {
    this.checkIsLogin();
    this.getAmazonData();
  }

  checkIsLogin() {
    let user = sessionStorage.getItem("user");
    if (user) {
      this.setState({
        islogin: true,
        show: false
      });
    }
  }
  changePlat(a) {
    this.props.dispatch({ type: "PLATCHANGE", plat: a });
  }
  changFormStatus(a, b, c) {
    this.setState(
      {
        [a]: {
          ...this.state[a],
          [b + "_status"]: c
        }
      },
      () => {}
    );
  }
  changeAccount(a, b, c) {
    this.setState({
      [a]: {
        ...this.state[a],
        [b]: c
      }
    });
  }
  addCount() {
    let { platform, amazon, shopee } = this.state;
    if (platform === "amazon") {
      if (amazon.store_name == "") {
        this.changFormStatus("amazonStatus", "store_name", "error");
        return;
      }
      if (amazon.seller_id == "") {
        this.changFormStatus("amazonStatus", "seller_id", "error");
        return;
      }
      if (amazon.token == "") {
        this.changFormStatus("amazonStatus", "token", "error");
        return;
      }
      $.ajax({
        method: "post",
        url: "http://118.25.155.176:8080/authSave",
        /* xhrFields: {
          withCredentials: true
        },
        crossDomain: true, */
        data: { ...amazon, platform },
        dataType: "json",
        success: res => {
          console.log(res);
          if (res.state === 1) {
            this.setState({
              show: false
            });
          }
          /* let data = res.data;
        this.setState({
          data
        }); */
        }
      });
      /* this.setState({
        show: false
      }); */
    } else {
      this.setState({
        show: false
      });
    }
  }
  showModal(a) {
    this.setState({
      show: true,
      platform: a
    });
  }

  getAmazonData() {
    let { amazonPages } = this.state;
    $.ajax({
      method: "get",
      url: "http://118.25.155.176:8080/auth",
      /*  xhrFields: {
        withCredentials: true
      },
      crossDomain: true, */
      data: { page: amazonPages.page, pageSize: amazonPages.pageSize },
      dataType: "json",
      error: err => {
        console.log(err);
      },
      success: (res, status, xhr) => {
        if (res.state === 1) {
          if (res.data && res.data.data) {
            this.setState({
              amazoonData: res.data.data,
              amazonPages: {
                ...this.state.amazonPages,
                total: res.data.last_page
              }
            });
          }
        }

        // console.log($.cookie("csrftoken"));
      }
    });
  }
  onAuthCheck(usa_id) {
    $.ajax({
      method: "post",
      url: "http://118.25.155.176:8080/authCheck",
      /*  xhrFields: {
        withCredentials: true
      },
      crossDomain: true, */
      data: { auth_id: usa_id },
      dataType: "json",
      error: err => {
        this.setState({
          tipMessage: "",
          tipModalShow: true
        });
      },
      success: res => {
        if (res.message === "Success") {
          this.setState({
            tipMessage: "",
            tipModalShow: true
          });
        } else {
          this.setState({
            tipMessage: "",
            tipModalShow: true
          });
        }

        // console.log($.cookie("csrftoken"));
      }
    });
  }

  render() {
    const {
      islogin,
      show,
      platform,
      shopee,
      amazon,
      amazoonData,
      siteList,
      amazonStatus,
      amazonPages
    } = this.state;
    // console.log(this.props.state);

    return (
      <div className="home B2c">
        {islogin ? (
          <div className="home_left">
            <SideBar {...this.props} />
          </div>
        ) : null}

        <div
          className="home_right"
          style={{ paddingLeft: islogin ? "118px" : 0 }}
        >
          <Topbar
            {...this.props}
            islogin={this.state.islogin}
            changePlat={a => {
              this.changePlat(a);
            }}
          />
          <ReturnTop />

          <div className="main">
            <Button
              onClick={() => {
                this.showModal("amazon");
              }}
            >
              
            </Button>
            <Link
              className="tip_note"
              to="/sells/b2c_platforms/note"
              target="_blank"
            >
              Amazon
            </Link>
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {amazoonData.map((a, b) => {
                  return (
                    <tr key={b}>
                      <td> {a.store_name} </td>
                      <td>{`${a.usa_add_time}`}</td>
                      <td>{a.usa_auth_status === 0 ? "" : ""}</td>
                      <td>{country[a.usa_region]}</td>
                      <td>
                        <Button
                          onClick={() => {
                            this.onAuthCheck(a.usa_id);
                          }}
                        >
                          
                        </Button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </Table>
            {amazonPages.total > 1 ? (
              <Pagination
                page={amazonPages.page}
                total={amazonPages.total}
                pageSize={amazonPages.pageSize}
                onPageChange={page => {
                  this.setState(
                    {
                      amazonPages: {
                        ...amazonPages,
                        page
                      }
                    },
                    () => {
                      this.getAmazonData();
                    }
                  );
                }}
                onPageSizeChange={pageSize => {
                  this.setState(
                    {
                      amazonPages: {
                        ...amazonPages,
                        pageSize,
                        page: 1
                      }
                    },
                    () => {
                      this.getAmazonData();
                    }
                  );
                }}
              />
            ) : null}
            <Button
              onClick={() => {
                this.showModal("shopee");
              }}
            >
              shopee
            </Button>
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>shopee</th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th>API</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>1</td>
                  <td>Mark</td>
                  <td>Otto</td>
                  <td>@mdo</td>
                  <td>@mdo</td>
                  <td>@mdo</td>
                </tr>
              </tbody>
            </Table>
          </div>
          <Footer />
        </div>
        <Modal
          dialogClassName="b2c_modal"
          show={show}
          onHide={() => {
            this.setState({
              show: false,
              amazonStatus: {
                store_name_status: "",
                seller_id_status: "",
                token_status: ""
              }
            });
          }}
        >
          <Modal.Header closeButton>
            <Modal.Title></Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {platform === "shopee" ? (
              <div className="note">
                <p></p>
                <h4>
                  1.shopee 270
                </h4>
                <h4>
                  2.shopeeshopee
                  
                </h4>
              </div>
            ) : (
              <div className="note">
                <p></p>
                <h4 className="text-danger">
                   amazon
                  amazon,;
                </h4>
              </div>
            )}
            {platform === "shopee" ? (
              <ul className="inputs">
                <li>
                  <p>
                    shopee:shopee
                  </p>
                  <input
                    type="text"
                    placeholder=""
                    value={shopee.account}
                    onChange={e => {
                      this.changeAccount("shopee", "account", e.target.value);
                    }}
                  />
                </li>
                <li>
                  <p>:2-3</p>
                  <input
                    type="text"
                    placeholder=""
                    value={shopee.account2}
                    onChange={e => {
                      this.changeAccount("shopee", "account2", e.target.value);
                    }}
                  />
                </li>
                <li>
                  <p>
                    :Paypalshopee,shopee
                  </p>
                  <input
                    type="text"
                    placeholder=""
                    value={shopee.recive}
                    onChange={e => {
                      this.changeAccount("shopee", "recive", e.target.value);
                    }}
                  />
                </li>
              </ul>
            ) : (
              <ul className="inputs">
                <li>
                  <p>
                    :
                  </p>
                  <input
                    type="text"
                    placeholder=""
                    className={
                      amazonStatus.store_name_status == "error"
                        ? "err_input"
                        : ""
                    }
                    value={amazon.store_name}
                    onChange={e => {
                      this.changFormStatus("amazonStatus", "store_name", "");
                      this.changeAccount(
                        "amazon",
                        "store_name",
                        e.target.value
                      );
                    }}
                  />
                  {amazonStatus.store_name_status == "error" ? (
                    <span className="note"></span>
                  ) : null}
                </li>
                {/*  <li>
                  <p>::2-3</p>
                  <input
                    type="text"
                    placeholder=""
                    value={amazon.nick_name}
                    onChange={e => {
                      this.changeAccount("amazon", "nick_name", e.target.value);
                    }}
                  />
                </li> */}
                <li>
                  <p>:,</p>
                  <input
                    type="text"
                    placeholder=""
                    value={amazon.seller_id}
                    className={
                      amazonStatus.seller_id_status == "error"
                        ? "err_input"
                        : ""
                    }
                    onChange={e => {
                      this.changFormStatus("amazonStatus", "seller_id", "");
                      this.changeAccount("amazon", "seller_id", e.target.value);
                    }}
                  />
                  {amazonStatus.seller_id_status == "error" ? (
                    <span className="note"></span>
                  ) : null}
                </li>
                <li>
                  <p></p>
                  <Dropdown>
                    <Dropdown.Toggle>{country[amazon.site]}</Dropdown.Toggle>

                    <Dropdown.Menu>
                      {siteList.map((a, b) => {
                        return (
                          <Dropdown.Item
                            key={b}
                            onClick={() => {
                              this.setState({
                                amazon: {
                                  ...amazon,
                                  site: a
                                }
                              });
                            }}
                          >
                            {country[a]}
                          </Dropdown.Item>
                        );
                      })}
                    </Dropdown.Menu>
                  </Dropdown>
                </li>
                <li>
                  <p>MWS:,</p>
                  <input
                    value={amazon.token}
                    className={
                      amazonStatus.token_status == "error" ? "err_input" : ""
                    }
                    onChange={e => {
                      this.changFormStatus("amazonStatus", "token", "");
                      this.changeAccount("amazon", "token", e.target.value);
                    }}
                    type="text"
                    placeholder="MWS"
                  />
                  {amazonStatus.token_status == "error" ? (
                    <span className="note">MWS</span>
                  ) : null}
                </li>
                <li>
                  <p></p>
                  <span></span>{" "}
                  <input
                    type="checkbox"
                    style={{
                      display: "inline-block",
                      width: "20px",
                      height: "20px",
                      verticalAlign: "middle",
                      marginRight: "30px"
                    }}
                    checked={amazon.status === 1}
                    onChange={e => {
                      if (e.target.checked) {
                        this.setState({
                          amazon: {
                            ...amazon,
                            status: 1
                          }
                        });
                      }
                    }}
                  />
                  <span></span>
                  <input
                    type="checkbox"
                    checked={amazon.status === 0}
                    style={{
                      display: "inline-block",
                      width: "20px",
                      height: "20px",
                      verticalAlign: "middle"
                    }}
                    onChange={e => {
                      if (e.target.checked) {
                        this.setState({
                          amazon: {
                            ...amazon,
                            status: 0
                          }
                        });
                      }
                    }}
                  />
                </li>
              </ul>
            )}
          </Modal.Body>
          <Modal.Footer>
            <Button
              variant="secondary"
              onClick={() => {
                this.setState({ show: false });
              }}
            >
              
            </Button>
            <Button
              variant="primary"
              onClick={() => {
                this.addCount();
              }}
            >
              
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal
          show={this.state.tipModalShow}
          className="order_send_modal"
          onHide={() => {
            this.setState({
              tipModalShow: false
            });
          }}
        >
          <Modal.Body>{this.state.tipMessage}</Modal.Body>

          <Modal.Footer>
            <Button
              variant="primary"
              onClick={() => {
                this.setState({
                  tipModalShow: false
                });
              }}
            >
              
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    state
  };
};

const mapDispatchToProps = dispatch => {
  return {
    dispatch
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(B2c_platforms);
